import React, { Component } from "react"

import { Link } from "react-router-dom"
import { Grid, Button, Toast, Modal } from "antd-mobile"

import { BASE_URL } from "../../utils/axios"

import styles from "./index.module.css"
import { getUser, userLogout } from "../../utils/api/Login"
import { getToken, isAuth, removeToken } from "../../utils"

// 
const menus = [
  { id: 1, name: "", iconfont: "icon-coll", to: "/favorate" },
  { id: 2, name: "", iconfont: "icon-ind", to: "/rent" },
  { id: 3, name: "", iconfont: "icon-record" },
  {
    id: 4,
    name: "",
    iconfont: "icon-identity",
  },
  { id: 5, name: "", iconfont: "icon-myinfo" },
  { id: 6, name: "", iconfont: "icon-cust" },
]

// 
const DEFAULT_AVATAR = BASE_URL + "/img/profile/avatar.png"

export default class Profile extends Component {
  state = {
    userInfo: {},
    isLogin: isAuth(),
  }

  componentDidMount() {
    this.getUserInfo()
  }
  // 
  getUserInfo = async () => {
    const { userInfo, isLogin } = this.state
    if (isLogin) {
      const { status, body } = await getUser(getToken())
      if (status === 200) {
        if (body.avatar) {
          body.avatar = `${BASE_URL}${body.avatar}`
        }
        this.setState({
          userInfo: body,
        })
      }
    } else {
    }
  }

  // 
  userLogout = () => {
    Modal.alert("", "???", [
      { text: "" },
      {
        text: "",
        onPress: async () => {
          const { status, description } = await userLogout()
          if (status === 200) {
            Toast.info(description, 2)
            removeToken()
            this.setState({
              userInfo: {},
              isLogin: false,
            })
          }
        },
      },
    ])
  }

  render() {
    const { history } = this.props
    const { userInfo, isLogin } = this.state

    return (
      <div className={styles.root}>
        {/*  */}
        <div className={styles.title}>
          <img
            className={styles.bg}
            src={BASE_URL + "/img/profile/bg.png"}
            alt=""
          />
          <div className={styles.info}>
            <div className={styles.myIcon}>
              <img
                className={styles.avatar}
                src={userInfo.avatar || DEFAULT_AVATAR}
                alt="icon"
              />
            </div>
            <div className={styles.user}>
              <div className={styles.name}>{userInfo.nickname ?? ""}</div>
              {/*  */}
              {isLogin && (
                <>
                  <div className={styles.auth}>
                    <span onClick={this.userLogout}></span>
                  </div>
                  <div
                    className={styles.edit}
                    onClick={() => history.push("/rent/add")}
                  >
                    
                    <span className={styles.arrow}>
                      <i className="iconfont icon-arrow" />
                    </span>
                  </div>
                </>
              )}

              {/*  */}
              {!isLogin ? (
                <div className={styles.edit}>
                  <Button
                    type="primary"
                    size="small"
                    inline
                    onClick={() => history.push("/login")}
                  >
                    
                  </Button>
                </div>
              ) : null}
            </div>
          </div>
        </div>

        {/*  */}
        <Grid
          data={menus}
          columnNum={3}
          hasLine={false}
          renderItem={(item) =>
            item.to ? (
              <Link to={item.to}>
                <div className={styles.menuItem}>
                  <i className={`iconfont ${item.iconfont}`} />
                  <span>{item.name}</span>
                </div>
              </Link>
            ) : (
              <div className={styles.menuItem}>
                <i className={`iconfont ${item.iconfont}`} />
                <span>{item.name}</span>
              </div>
            )
          }
        />

        {/*  */}
        <div className={styles.ad}>
          <img src={BASE_URL + "/img/profile/join.png"} alt="" />
        </div>
      </div>
    )
  }
}
